import { Instance, types } from 'mobx-state-tree';

export const InventoryItem = types
    .model("InventoryItem", {
        id: types.maybe(types.string),
        name: types.string,
        price: types.number
    })
    .actions(self => ({
        setName(name: string) {
            self.name = name;
        },

        setPrice(price: number) {
            self.price = price;
        }
    }));

export type IInventoryItem = Instance<typeof InventoryItem>